* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.container {
  box-shadow: 0px 0px 12px #ccc;
  border-radius: 8px;
  padding: 2rem;
  background-color: rgb(241, 206, 206);
  border: rgb(89, 88, 88) solid 0.2px;
  width: 25rem;
  height: 30rem;
}

input {
  height: 25px;
}

label {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 1.3rem;
}

input {
  width: 100%;
  font-size: 1.2rem;
  padding: 15px 4px;
  margin: 8px 0;
  border-radius: 8px;
}

.btn {
  display: block;
  width: 40%;
  font-size: 1.2rem;
  margin: 8px auto;
  padding: 8px 0;
  background-color: #eb2222;
  color: #fff;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
}
/* unvisited link */

.btn-outline {
  background-color: #fff;
  color: #021127;
}

.center {
  text-align: center;
  margin: 24px 0;
}

p,
h3 {
  margin-top: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(33, 32, 32);
}

p {
  font-size: 1rem;
}

.img-container {
  text-align: center;
}

.img-container img {
  height: 200px;
}
